<template>
  <div>
    <Header-direction headertit="" headertext=""></Header-direction>
    <div class="public-container">
      <div class="day">
        <p class="day-p"></p>
        <div class="history-line"></div>
      </div>
      <History-list-bg v-for="item in contenttxt" :key="item.id">
        <p v-text="item.destroytext" slot="historytext"></p>
        <span v-text="item.price" slot="prices"></span>
        <img :src="item.pic" alt="" slot="historypic">
      </History-list-bg>
      <div class="day">
        <p class="day-p"></p>
        <!--<div class="history-line"></div>-->
      </div>
      <History-list-bg v-for="item in contenttxt2" :key="item.id">
        <p v-text="item.destroytext" slot="historytext"></p>
        <span v-text="item.price" slot="prices"></span>
        <img :src="item.pic" alt="" slot="historypic">
      </History-list-bg>
      <div class="day">
        <p class="day-p"></p>
        <div class="history-line1"></div>
      </div>
      <History-list-bg v-for="item in contenttxt3" :key="item.id">
        <p v-text="item.destroytext" slot="historytext"></p>
        <span v-text="item.price" slot="prices"></span>
        <img :src="item.pic" alt="" slot="historypic">
      </History-list-bg>

    </div>
  </div>
</template>

<script>
import HeaderDirection from '../../components/header-top-item.vue'
import HistoryListBg from '../../components/history-list-bg.vue'
  export default {
    data(){
      return{
        contenttxt:[
          {destroytext:'+++ 874...',
            price:'2999',
            pic:'src/assets/images/history-lis0421_03.jpg'
          },
          {destroytext:'65(5) 1...',
            price:'3969',
            pic:'src/assets/images/history-lis0421_06.jpg'
          },
          {destroytext:'++ ++++...',
            price:'3021',
            pic:'src/assets/images/history-lis0421_08.jpg'
          }
        ],
        contenttxt2:[
          {destroytext:'++ ++++...',
            price:'5596',
            pic:'src/assets/images/history-lis0421_10.jpg'
          }
        ],
       contenttxt3:[
          {destroytext:'75(5)2+3...',
            price:'5963',
            pic:'src/assets/images/history-lis0421_12.jpg'
          },
         {destroytext:'++4(4) ...',
            price:'2966',
            pic:'src/assets/images/history-lis0421_14.jpg'
          },

        ]

      }
    },
     components:{
       HeaderDirection,
       HistoryListBg
     }

  }
</script>

<style>
  .public-container {width:17.6rem;margin:0 auto;position:relative;}
  .day {float:left;margin-top:0.2rem;width:1.575rem;position:relative;}
  .day-p {font-size:0.7rem;}
  .history-line {width:1px;height:9rem;background: #9a9a9a;position:relative;left:0.7rem;top:1.2rem;}
  .history-line1 {width:1px;height:4.3rem;background: #9a9a9a;position:relative;left:0.7rem;top:1.2rem;}
</style>



